* {
  margin: 0;
}

a, a:visited {
  text-decoration: none;
}

body {
  margin: 0;
}

#logo {
  position: relative;
  width: 150px;
  height: 150px;
  /* top: 10px; */
  left: 4.5%;
}

.innerBox {
  position: absolute;
  background-color: #EFFAFD;
  width: 900px;
  height: 600px;
  box-shadow: 0 0 30px grey;
  border-radius: 60px;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}

.menuNavBar {
  list-style-type: none;
  position: absolute;
  top: 10%;
  right: 5%;
  
}

.menuNavBar > li {
  float:left;
}


.contentBox {
  position: relative;
  width: 360px;
  left: 5%;
  top: 30px;
}

.contentByHeader > p{
  font-family: 'Raleway', sans-serif;
  font-size: 15px;
  margin-top: 20px;
  text-align: justify;
  text-shadow: 0 0 1px grey;
}

.contentByHeader a {
  color: rgb(255, 0, 0);
  font-size: 18px;
  text-shadow: 1px 1px 3px rgb(255, 87, 87);
}

.contentBox > h1{
  font-family: 'Open Sans', sans-serif;
  font-size: 32px;
  margin-bottom: 10%;
}

.imgAvatarBox {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 200px;
  top: 23%;
  left: 51%;
}

.imgAvatarBox > img {
  position: absolute;
  width: 250px;
  height: 250px;
  right: 50%;
  top: 50%;
  transform: translate(50%,-50%);
  /* background-color: rgb(237, 188, 85, 0.4); */
}

#myAvatarImage {
  position: absolute;
  right: 50%;
  top: 50%;
  transform: translate(50%,-50%);
}

strong {
  margin-left: 200px;
}